.foodItem {
  position: relative;
  margin-top: 180px;
  margin-left: 15px;
  width: 250px;
  padding: 20px 25px;
  border: 2px solid rgb(223, 223, 223);
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  gap: 10px;
}

.foodItem:hover {
  background-color: rgb(245, 245, 245);
}

.foodItem img {
  transform: translateX(15px);
  margin-top: -150px;
  margin-bottom: -100px;
  width: 150%;
}

.foodItem .foodDesc {
  margin-top: -25px;
  margin-bottom: -20px;
  font-size: smaller;
  color: rgb(194, 194, 194);
}

.foodPrice {
  margin-bottom: 0px;
}

.errorMsg {
  bottom: -35px;
  position: absolute;
  color: red;
  font-size: 12px;
}
